<template>
    <div>
        <van-nav-bar
        title="验证码"
        left-text="返回"
        @click-left="toback"
        />
        <van-form>
          <van-field
            v-model="email"
            type="text"
            label="邮箱"
            placeholder="邮箱地址"
          />
          <!-- key: 防止当前表单域会将内容携带给其他表单域 -->
          <van-field key="code" v-model="code" type="text" label="验证码" placeholder="6位验证码">
            <template #button>
              <van-button size="small" type="info" @click="sendCode" >{{text}}</van-button>
            </template>
          </van-field>
          <div class="register-btn">
            <van-button round block type="info" native-type="submit" @click="next">下一步</van-button>
          </div>
        </van-form>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                email:'',
                code:'',
                text:'发送验证码'
            }
        },
        methods:{
            toback(){
                this.$router.go(-1)
            },
            Send(){
                
            },
            sendCode(){
                // if (!validForm.validUserForm({email: this.email})) {
                //     return;
                // }
                let mm = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
                if(mm.test(this.email)){
                    let time = 60;
                    this.text = time + '秒后重新发送';
                    this.isSend = true;
                    let timer = setInterval(() => {
                        if (time == 0) {
                        clearInterval(timer);
                        timer = null;
                        this.text = '发送验证码';
                        this.isSend = false;
                        } else {
                        time--;
                        this.text = time + '秒后重新发送';
                        }
                    }, 1000);
                    this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "POST",
                        url: "/emailValidCode",
                        data: {
                            appkey: this.appkey,
                            email:this.email
                        }
                    }).then(result => {
                        this.$toast.clear(); 
                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
                } else{
                    this.$toast('邮箱错误')
                }
                
            },
            next(){
                let mm = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
                if(mm.test(this.email)){
                    this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });

                    this.axios({
                        method: "POST",
                        url: "/checkValidCode",
                        data: {
                            appkey: this.appkey,
                            validCode: this.code
                        }
                    })
                    .then(result => {
                        this.$toast.clear();
                        
                        if (result.data.code == 'K001') {
                            this.$router.push({name:'Next'})
                        } else {
                            this.$toast(result.data.msg);
                        }
                        
                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
                }else{
                    this.$toast('邮箱错误')
                }
            }
        }
    }
</script>

<style lang="less" scoped>
/deep/.van-button--info{
    background-color: #f4b973;
    border: 1px solid #f4b973;
}
</style>